<template>
  <div>
    <van-nav-bar title="领劵中心" left-arrow @click-left="res" />
    <div class="top-img">
      <img
        src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1584115603844&di=f8d1049bd1fa8c3baa834189f5cd6e67&imgtype=0&src=http%3A%2F%2F5b0988e595225.cdn.sohucs.com%2Fimages%2F20171102%2Fa09e1cc53d4d41c99f5eed4180c062f0.jpeg"
        alt
      />
    </div>
    <van-row class="box-row" v-for="(item,index) in $store.state.home.couponlist" :key="index">
      <van-col span="18" class="col-18-box" :style="{background:item.color}">
        <div class="left">
          <van-icon name="bill-o" />
        </div>
        <div class="center">
          <p class="text">{{item.text}}</p>
          <p class="time">{{item.time}}</p>
        </div>
      </van-col>

      <van-col span="6" class="col-6-box">
        <div class="box-right">
          <div class="price">
            ￥
            <span>{{item.money}}</span>
          </div>
          <div class="btn" @click="addCoupon(index)">{{item.receive}}</div>
        </div>
      </van-col>
    </van-row>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  methods: {
    addCoupon(index) {
      if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      if (this.$store.state.home.couponlist[index].bool) {
        this.$store.state.home.couponlist[index].bool = false;
        Toast("领取成功");
        this.$store.state.home.couponlist[index].receive = "去使用";
        this.$store.state.myjp.myCoupon.push(
          this.$store.state.home.couponlist[index]
        );

        this.$store.state.myjp.useCoupon.push({
          available: this.$store.state.home.couponlist[index].money,
          condition: `满${this.$store.state.home.couponlist[index].money +
            0.01}元\n减${this.$store.state.home.couponlist[index].money}元 `,
          reason: "",
          apply: this.$store.state.home.couponlist[index].money + 0.01,
          value: this.$store.state.home.couponlist[index].money * 100,
          name: this.$store.state.home.couponlist[index].text,
          startAt: 1489104000,
          endAt: 1514592000,
          valueDesc: this.$store.state.home.couponlist[index].money,
          unitDesc: "元",
          bool:true,
          color:this.$store.state.home.couponlist[index].color,
          time:this.$store.state.home.couponlist[index].time
        });
      } else {
        this.$router.push("/shoppingClass");
      }
    },
    res(){
      this.$router.push('/home')
      this.$store.state.bool = true
    }
  },
  created() {
    this.$store.state.bool = false;
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    // this.$store.state.bool = true;
  }
};
</script>

<style lang="scss" scoped>
.top-img {
  img {
    width: 100%;
  }
}
.box {
  width: 50%;
  img {
    width: 100%;
  }
}
.box-row {
  padding: 0 5px;
  height: 80px;
  margin: 10px 0;
}
.col-18-box {
  height: 100%;
  opacity: 0.6;
  background: #ff5001;
  color: #fff;
  .left {
    display: inline-block;
    width: 20%;
    height: 100%;
    font-size: 40px;
    line-height: 80px;
    text-align: center;
  }
  .center {
    display: inline-block;
    width: 80%;
    font-size: 12px;
    p {
      margin: 8px 0;
    }
  }
}
.col-6-box {
  background: #ff5001;
  height: 100%;
  position: relative;
  //  line-height: 80px;
}
.box-right {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.price {
  text-align: center;
  color: #fff;
  span {
    font-size: 18px;
  }
}
.btn {
  width: 40px;
  height: 20px;
  line-height: 20px;
  margin: 0 auto;
  font-size: 12px;
  border: 1px solid #fff;
  color: #fff;
  text-align: center;
  margin: 5px;
}
</style>